.audio-pop-up {
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 6vh;
    padding: 4vw;
    position: relative;
    background-color: white;
    border-radius: 50px;
  }
  
  .audio-pop-up .text-wrapper {
    font-family: "TT Norms Pro-Regular", Helvetica;
    font-weight: 400;
    color: #000000;
    font-size: 4vw;
    position: relative;
    width: fit-content;
    margin-top: -1px;
    text-align: center;
    letter-spacing: 0;
    line-height: normal;
  }
  
  .audio-pop-up .button-accept {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    padding: 4vw, 4vw, 4vw;
    position: relative;
    align-self: stretch;
    width: 100%;
    background-color: #1c1c1c;
    border-radius: 100px;
  }
  
  .audio-pop-up .div {
    font-family: "TT Norms Pro-Bold", Helvetica;
    font-weight: bold;
    color: #ffffff;
    font-size: 4vw;
    white-space: nowrap;
    position: relative;
    width: fit-content;
    margin-top: -1px;
    text-align: center;
    letter-spacing: 0;
    line-height: normal;
  }
